import React, { Component } from 'react';
import Title from './component/Title';
import Control from './component/Control';
import Form from './component/Form';
import ListItem from './component/ListItem';
import { filter, includes, orderBy as funcOrderBy, remove} from 'lodash';
const uuidv4 = require('uuid/v4');

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            items: [],
            isShowForm: false,
            strSearch: '',
            orderBy: 'name',
            orderDir: 'asc', // tăng
            itemSelected : null,
        }
        this.handleToogleForm = this.handleToogleForm.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.handleSort = this.handleSort.bind(this);
        this.handleDelete = this.handleDelete.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleEdit = this.handleEdit.bind(this);
    }
    componentWillMount(){
        let items = JSON.parse(localStorage.getItem('task'));
        this.setState({
            items: items,
        })
    }
    handleToogleForm() {
        this.setState({
            isShowForm: !(this.state.isShowForm),
            itemSelected: null, // khi edit xong nó ko mất đi
        })
    }
    handleSearch(value) {
        this.setState({
            strSearch: value,
        })
    }
    handleSort(by, dir) {
        this.setState({
            orderBy: by,
            orderDir: dir,
        })
    }
    handleDelete(id) {
        let items = this.state.items;
        remove(items, function(item) {
            return item.id === id;
    
        });
        this.setState({
            items: items
        })
        localStorage.setItem('task', JSON.stringify(items));
        
    }
    handleSubmit(item) {
        let items = this.state.items;
        if(item.id !== ''){
            items.forEach((ele,key)=>{
                if(ele.id === item.id){
                    items[key].name = item.name;
                    items[key].level = +item.level;
                }
            })
        } else {
            items.push({
                id: uuidv4(),
                name: item.name,
                level: +item.level,
            });
        }
        this.setState({
            items: items,
            isShowForm: false
        })
        localStorage.setItem('task', JSON.stringify(items));
    }
    handleEdit(item){
        this.setState({
            itemSelected: item,
            isShowForm: true,
        })
    }
    render() {
        let itemsOrigin = (this.state.items !== null) ? [...this.state.items] : [];
        let items = [];
        let { orderBy, orderDir } = this.state;
        const search = this.state.strSearch;
        let itemSelected = this.state.itemSelected;
        //Search key word
        items = filter(itemsOrigin, (item) => {
            return includes(item.name.toLowerCase(), search.toLowerCase());
        })
        items = funcOrderBy(items, [orderBy], [orderDir]);
        let isShowForm = this.state.isShowForm;
        let elemtForm = null;
        // let strSearch   = this.state.strSearch;
        if (isShowForm === true) {
            elemtForm = <Form
                onClickAdd  = {this.handleToogleForm}
                onClickSubmit    =  {this.handleSubmit}
                itemSelected = {itemSelected}
            />;
        }
        // items.forEach(ele => {
        //     console.log(ele.id);
        // });
        return (
            <div className="row">
                <div className="container">
                    {/* TITLE : START */}
                    <Title />
                    {/* TITLE : END */}
                    {/* CONTROL (SEARCH + SORT + ADD) : START */}
                    <Control
                        orderBy={orderBy}
                        orderDir={orderDir}
                        onClickAdd={this.handleToogleForm}
                        isShowForm={isShowForm}
                        onClickSearchGo={this.handleSearch}
                        onClickSortGo={this.handleSort}
                    />
                    {/* CONTROL (SEARCH + SORT + ADD) : END */}
                    {/* FORM : START */}
                    {elemtForm}
                    {/* FORM : END */}
                    {/* LIST : START */}
                    <ListItem 
                        items={items} 
                        isShowForm={isShowForm}
                        onClickDeleteGo = {this.handleDelete}
                        onClickEditGo = {this.handleEdit}
                    />
                        <div className="author" >
                            Designed and Coded By
                            <br /><a href="https://www.facebook.com/hoan.phi.79">Phi Xuan Hoan</a>
                        </div>
                </div>
            </div>
        );
    }
}

export default App;
