import React, { Component } from 'react';
class Item extends Component {
    constructor(props) {
        super(props)
        this.handleDelete = this.handleDelete .bind(this);
        this.handleEdit = this.handleEdit.bind(this);
        this.showItemLevel = this.showItemLevel.bind(this);
    }
    handleDelete(){
        this.props.onClickDelete(this.props.item.id);
    }
    handleEdit(item){
        this.props.onClickEdit(item);
    }
    // function set text level
    showItemLevel(level) {
        let elemtLevel = null;
        if (level === 0) {
            elemtLevel = <td className="text-center"><span className="label label-success">small</span></td>
        } else if (level === 1) {
            elemtLevel = <td className="text-center"><span className="label label-warning">medium</span></td>
        } else if (level === 2) {
            elemtLevel = <td className="text-center"><span className="label label-danger">hight</span></td>
        }
        return elemtLevel;
    }
    // render
    render() {
        const item = this.props.item;
        const index = this.props.index;

        return (
            <tr>
                <td className="text-center">{index + 1}</td>
                <td>{item.name}</td>
                {this.showItemLevel(item.level)}
                <td>
                    <div className="btn-group">
                        <button onClick = {() => this.handleEdit(item)} type="button" className="btn btn-warning">Edit</button>
                        <button onClick = {this.handleDelete} type="button" className="btn btn-danger">Delete</button>
                    </div>
                    
                </td>
            </tr>
        );
    }
}

export default Item;
