import React, { Component } from 'react';
import Item from './Item'
class ListItem extends Component {

    render() {
        const items = this.props.items;
        const elmtItem = items.map((item, index) =>
            <Item
                key={index}
                item={item}
                index={index}
                onClickDelete={this.props.onClickDeleteGo}
                onClickEdit={this.props.onClickEditGo}
            />
        );
        return (
            <div className="panel panel-success">
                <div className="panel-heading "><p>&nbsp;</p>
                    <div id="date" className="row">
                        <div className="col-lg-6">
                            <div className="input-group">
                                <span className="input-group-btn">
                                <button id="btn" className="glyphicon glyphicon-calendar btn btn-default" ></button>
                                </span>
                                <input id="datepicker" type="text" className="form-control"/>
                            </div>{/* /input-group */}
                    </div>
                </div>
            </div>
            <table className="table table-hover ">
                <thead>
                    <tr>
                        <th style={{ width: '10%' }} className="text-center">No</th>
                        <th>Task</th>
                        <th style={{ width: '20%' }} className="text-center">Level</th>
                        <th style={{ width: '20%' }}>Action</th>
                    </tr>
                </thead>
                <tbody>
                    {elmtItem}
                </tbody>
            </table>
            </div >

        );
    }
}

export default ListItem;
