import React, { Component } from 'react';
class Form extends Component {
    constructor(props) {
        super(props);
        this.state = {
            taskid: '',
            taskname: '',
            tasklevel: 0,
        }
        this.handleCancel = this.handleCancel.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        
    }
    handleCancel() {
        this.props.onClickAdd();
    }
    handleChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;
        this.setState({
            [name]: value,
        })
    }
    handleSubmit(event) {
        let item = {
            id: this.state.taskid,
            name: this.state.taskname,
            level: this.state.tasklevel,
        }
        if(this.state.taskname !== ''){
            this.props.onClickSubmit(item);
        } else {
            alert("Fill name of todo item !")
        }
        event.preventDefault();
    }
    componentWillMount(){
        let item = this.props.itemSelected;
        if(item !== null){
            this.setState({
                taskid: item.id,
                taskname: item.name,
                tasklevel: item.level,
            })
        }
    }
    componentWillReceiveProps(nextProps){
        let item = nextProps.itemSelected;
        if(nextProps !== null){
            this.setState({
                taskid: item.id,
                taskname: item.name,
                tasklevel: item.level,
            })
        }
    }
    render() {
        
        return (
            <div className="row">
                <div className="col-md-offset-7 col-md-5">
                    <form  className="form-inline">
                    
                        <div className="form-group">
                            <label className="sr-only" >label</label>
                            <input type="text" value = {this.state.taskname} onChange={this.handleChange} name="taskname"  className="form-control" placeholder="Task Name"  />
                        </div>

                        <div className="form-group">
                            <label className="sr-only" >label</label>
                            <select value = {this.state.tasklevel} onChange={this.handleChange} name="tasklevel"  id="inputDs" className="form-control" required="required">
                                <option value = {0} >Small</option>
                                <option value = {1}>Medium</option>
                                <option value = {2}>High</option>
                            </select>
                        </div>
                        <button onClick={this.handleSubmit}  type="submit" className="btn btn-primary">Submit</button>
                        <button onClick = {this.handleCancel} type="button"  className="btn btn-default">Cancel</button>
                    </form>
                </div>


            </div>
        );
    }
}

export default Form;
